.layout {
  background-color: #2A2A2A;
  min-width: 390px;
}
.header {
  box-shadow: 0 6px 8px 5px rgba(0, 0, 0, 0.5);
}
.logo {
  float: left;
  width: 64px;
  height: 64px;
  background-image: url(/static/media/breaking_bad.e2d8bc80.jpg);
  background-size: 64px 64px;
}
.slogen {
  margin-left: 30px;
  float: left;
  color: #04faac;
  font-size: 1.5rem;
  font-family: Microsoft YaHei;
}
.menu-horizontal {
  float: left;
  margin-left: 30px;
}
.menu-vertical {
  position: absolute;
  left: 0;
  width: 50px;
  height: 64px;
}
.menu-icon {
  position: absolute;
  top: -40px;
  left: 12px;
  font-size: 24px !important;
  color: #1DA57A;
}
@media (min-width: 1140px) {
  .menu-horizontal {
    display: block;
  }
  .menu-vertical {
    display: none;
  }
}
@media (max-width: 1140px) {
  .menu-horizontal {
    display: none;
  }
}
@media (max-width: 700px) {
  .slogen {
    display: none;
  }
}
.search {
  max-width: 300px;
  float: right;
  margin: 12px 60px 0 0;
}
@media (max-width: 520px) {
  .search {
    max-width: 200px;
  }
}
.avatar {
  background: #1DA57A;
  color: #ffffff;
  line-height: 30px;
}
.avatar .anticon {
  margin: 0;
}
.user-menu-box {
  position: absolute;
  right: 35px;
  z-index: 100;
}
@media (max-width: 1140px) {
  .search {
    margin: 12px 25px 0 0;
  }
  .user-menu-box {
    right: 0px;
  }
}
.content {
  min-height: 600px;
  padding: 20px 60px 0 60px;
}
@media (max-width: 520px) {
  .content {
    padding: 20px 30px 0 30px;
  }
}
.sub-title {
  color: #ffffff;
  font-size: 30px;
}
.back-to-top {
  cursor: pointer;
  background-color: #04faac;
  border-radius: 50%;
}
.ant-menu-sub {
  max-height: 500px;
  overflow-y: auto;
}
/*滚动条整体样式*/
.ant-menu-sub::-webkit-scrollbar {
  width: 10px;
  height: 1px;
}
/*滚动条滑块*/
.ant-menu-sub::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background: #1da57a;
}
/*滚动条轨道*/
.ant-menu-sub::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}
.ant-layout-footer {
  padding: 15px;
}
